	module.exports = {"data":{"nodePost":{"title":"R U Talkin' R.E.M. RE: ME?","link":"http://www.earwolf.com/show/u-talkin-u2-to-me/","body":{"value":"<p>It's very hard to explain why, but I consider '<a href=\"http://www.earwolf.com/show/u-talkin-u2-to-me/\">U Talkin' U2 2 Me?</a>' to be the greatest podcast of all time. Possibly just one of my favorite <em>things</em> of all time. I don't like U2 all that much, yet somehow hearing Scott and Scott break down U2's entire discography over 24 episodes was something I saved for special occasions and often laughed to the point of tears while listening to. And boy, did they ever spend a lot of time talking about the band U2 (not really.)  They even knew the names of every single member of the band. Truly encyclopedic knowledge.</p>\r\n\r\n<p>And now Scott and Scott are back with '<a href=\"http://www.earwolf.com/show/u-talkin-u2-to-me/\">R U Talkin' R.E.M. RE: ME?</a>' in which they explore the output of the band R.E.M in what I'm sure will be painstaking detail. I like R.E.M. more than I like U2, but I can't say I would ever want to go through their entire back catalog. Yet somehow here we are again, and I'm excited to hear all about the album Monster. Listening today was just like old times. Can't wait to save for long road trips and laugh to the point of nearly driving off the road.</p>\r\n\r\n <p>If you're looking for more info on what the hell this is all about, this <a href=\"https://www.stereogum.com/1981761/talkin-to-scott-aukerman-re-u-talkin-r-e-m-re-me/franchises/interview/\">Stereogum interview</a> is as good a place as any. It might just confuse you more, which is par for the course.</p>\r\n\r\n<p>Good ep.</p>"},"relationships":{"image":null},"created":1519265586}},"pathContext":{"slug":"/link/r-u-talkin'-r.e.m.-re:-me/"}}

